function ChildRouting() {
  return (
    <div className="text-primary text-center m-3 ">
      <h3>Hello From ChildComponets</h3>
      <p> you just clicked a ChildRoutingh link</p>
    </div>
  );
}

function Props() {
  return (
    <div className="text-danger text-center m-3 ">
      <h3>Hello From ChildComponets</h3>
      <p> you just clicked a StateAndProps link</p>
    </div>
  );
}
function QueryParams() {
  debugger;

    const QueryParams = new URLSearchParams(useLocation().search)
    let paramsValue = [];
    QueryParams.forEach(function (value, key) {
    const result = { key : key, value : value };
    paramsValue.push(result);
    });
    
    const elements = paramsValue.map((result) =>
    <div className="text-center shadow-sm">
     <h3 className="shadow-sm"> {result.key} : {result.value} </h3>
     </div>
    )

    return(<div>
        {elements}
    </div>)
  
}

function ChildComponets() {
  debugger;
  const { path, url } = useRouteMatch();
  return (
    <div className="container text-muted shadow-lg mt-3">
      <h1>Child Routing List</h1>
      <ul className="bg-light p-3 m-3">
        <li>
          <Link to={`${url}/ParaRouting`}>ExampleRouting</Link>
        </li>
        <li>
          <Link to={`${url}/childRouting1`}>ChildRouting</Link>
        </li>
        <li>
          <Link to={`${url}/props`}>StateAndProps</Link>
        </li>
        <li>
          <Link to={`${url}/Parameters`}>QueryParameter</Link>
        </li>
      </ul>

      <Switch>
        <Route path={`${path}/ParaRouting`}>
          <RoutingExample />
        </Route>
        <Route path={`${path}/childRouting1`}>
          <ChildRouting />
        </Route>
        <Route path={`${path}/props`}>
          <Props />
        </Route>
        <Route path={`${path}/:topicID`}>
          <QueryParams />
        </Route>
        <Route path={`${path}`}>Please choose the topic</Route>
      </Switch>
    </div>
  );
}

function RouteExample() {
  return (
    <div>
      <Router>
        <Navbar bg="dark" variant="dark" mb-3>
          <Navbar.Brand href="#home">ChildRouteExample </Navbar.Brand>
          <Navbar className="mr-auto m-2 p-3">
            <Link to="/">Home</Link>
            <span className="m-2">
              {" "}
              <Link to="/contact-us">Contact Us</Link>
            </span>
            <Link to="/about-us">About Us</Link>
            <h3 className="m-2">
              {" "}
              <Link to="/ChildComponets">ChildRouteClick</Link>
            </h3>
          </Navbar>
          <Form inline>
            <input type="text" placeholder="Search" className="mr-sm-2" />
            <Button variant="outline-info">Search</Button>
          </Form>
        </Navbar>

        <div>
          <Switch>
            <Route exact path="/">
              <HomePage />
            </Route>
            <Route path="/contact-us">
              <ContactUs />
            </Route>
            <Route path="/about-us" component={AboutUs}></Route>
            <Route exact path="/">
              <HomePage />
            </Route>
            <Route path="/Useful-links">
              <UsefulLink />
            </Route>
            <Route path="/Resources" component={Resources}></Route>
            <Route path="/Company" component={Company}></Route>
            <Route path="/ChildComponets">
              <ChildComponets />
            </Route>
            <Route path="*">404 page not found</Route>
          </Switch>
        </div>

        <MDBFooter
          color="blue"
          className="font-small bg-dark  text-primary pt-4 mt-4"
        >
          <MDBContainer fluid className="text-center text-md-left">
            <MDBRow>
              <MDBCol md="4">
                <h4 className="title p-2 m-3">Footer Content</h4>
                <p className="m-3 p-2">
                  Example of an Routing with Header And Footer
                </p>
              </MDBCol>
              <MDBCol md="4">
                <h4 className="title p-2 m-3">Links</h4>
                <ul>
                  <li className="list-styled">
                    <Link to="/">Home</Link>
                  </li>
                  <li className="list-styled">
                    <Link to="/Useful-links">Useful links</Link>
                  </li>
                  <li className="list-styled">
                    <Link to="/Resources">Resources</Link>
                  </li>
                  <li className="list-styled">
                    <Link to="/Company">Company</Link>
                  </li>
                </ul>
              </MDBCol>
              <MDBCol md="4">
                <h4 className="title  p-2 m-3">Links</h4>
                <ul>
                  <li className="list-unstyled">
                    <Link to="/">Home</Link>
                  </li>
                  <li className="list-unstyled">
                    <Link to="/Useful-links">Useful links</Link>
                  </li>
                  <li className="list-unstyled">
                    <Link to="/Resources">Resources</Link>
                  </li>
                  <li className="list-unstyled">
                    <Link to="/Company">Company</Link>
                  </li>
                </ul>
              </MDBCol>
            </MDBRow>
          </MDBContainer>
          <div className="footer-copyright bg-light text-center py-3">
            <MDBContainer fluid>
              &copy; {new Date().getFullYear()} Copyright:{" "}
              <a href="https://reactjs.org/"> ReactJs Tutorial</a>
            </MDBContainer>
          </div>
        </MDBFooter>
      </Router>
    </div>
  );
}

export default RouteExample;
